import React, { PureComponent } from "react";
import Card from './Elements/Card';
import "./scss/SquarePhoto.scss";

class SquarePhoto extends PureComponent {
  render() {
    const { postData, completeDataObject, adjustWidth, CardStyle, ThemeStyle, ProductSetting, ThemeInfo, clickToShowPopUp, cardNumber, postSizeInPx, onClickPopUpSlider } = this.props;
    let totalPostBestFit = postData.length;
    if (ThemeStyle?.bestFit === 1 && postData.length >= parseInt(cardNumber)) {
      const bestFitEachRow = Math.floor(postData.length / parseInt(cardNumber));
      totalPostBestFit = parseInt(cardNumber) * bestFitEachRow;
    }

    return (
      <div tabIndex="0" aria-label={`There are ${postData.length > 0 ? postData.length : 0} posts in the feed`} role="feed" className="tb_sp_post_container" >
        {postData.map((item, index) => {
          if (index >= totalPostBestFit) return null;
          const cardData = completeDataObject[item];
          return (
            <Card key={index} Post={cardData} ThemeInfo={ThemeInfo} index={index} adjustWidth={adjustWidth} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ProductSetting={ProductSetting} clickToShowPopUp={clickToShowPopUp} postSizeInPx={postSizeInPx} onClickPopUpSlider={onClickPopUpSlider} />
          );
        })}
      </div>
    );
  }
}


export default SquarePhoto;